.recipebook-area {
  background-color: #e2f3f5;
  display: flex;
  position: relative;
  flex-direction: row;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}
.recipebook-first-section {
  height: 100vh;
  width: 50vw;
  background-color: #e2f3f5;
}
.recipebook-list {
  height: 100vh;
  border-radius: 5px;
  background-color: #2e303f;
  border: 1px #6e3b3b solid;
}
.recipebook-second-section {
  height: 100vh;
  width: 50vw;
  background-color: #ffffff;
}
.recipebook-soup {
  width: 25vw;
}
.recipebook-knob-button {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #2e303f;
}
.recipebook-knob-button:hover > .recipe-button-icon {
  transform: rotate(25deg);
  background-image: url("../../assets/imgs/knobovenretactive.jpg");
}
.recipe-button-active > .recipe-button-icon {
  transform: rotate(25deg);
  background-image: url("../../assets/imgs/knobovenretactive.jpg");
}
.recipe-button-active {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #2e303f;
}
.recipe-button-icon {
  width: 2.4vw;
  height: 2.4vw;
  padding: 5%;
  margin-right: 10px;
  border-radius: 50%;
  background-image: url("../../assets/imgs/knobovenret.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: 0.6s ease;
}
.recipe-button-text {
  width: 4.5vw;
  font-size: 0.7vw;
  font-family: "OpenSans-Regular";
  text-align: left;
  white-space: nowrap;
}
.recipesList-main-area {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 10% 5% 5% 5%;
  font-family: "OpenSans-Regular";
  font-size: 1vw;
}
.recipesList-loading-area {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70vh;
}
.recipesList-item {
  background-color: #3c4049;
  width: 30vw;
  border-radius: 10px;
  overflow: hidden;
  transition: 0.5s ease;
  margin: 5px;
}
.recipesList-item:hover {
  transform: scale(0.985);
  cursor: pointer;
}
.recipesList-item-norecipes {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20vh;
  font-family: "OpenSans-SemiBold";
  font-size: 1.1vw;
  color: #ceced6;
}
.recipesList-item-title {
  font-family: "OpenSans-SemiBold";
  font-size: 1.1vw;
  text-align: left;
  color: #ceced6;
}
.recipesList-item-picture {
  width: 7.5vw;
  height: 7.5vw;
  object-fit: cover;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 5px;
  object-fit: cover;
}
.recipesList-item-author {
  display: flex;
  justify-content: flex-start;
  font-family: "OpenSans-Regular";
  font-size: 1vw;
  color: #ceced6;
}
.recipesList-item-time {
  display: flex;
  justify-content: flex-start;
  font-family: "OpenSans-Regular";
  font-size: 1vw;
  color: #ceced6;
}
.recipesList-item-timeago {
  font-family: "OpenSans-Regular";
  white-space: nowrap;
  font-size: 0.9vw;
  color: #edc23c;
}
.recipesList-item-icon {
  margin-right: 10px;
  color: #ee6670;
  width: 1vw;
}
.recipesList-item-simplebar {
  height: 99vh !important;
  z-index: 1;
}
.recipeDetails-main {
  padding-left: 2%;
  padding-right: 2%;
  width: 50vw;
  height: 80vh;
}
.recipeDetails-title {
  font-family: "Nunito-Bold";
  font-size: 2.1vw;
  border-bottom: 2px black solid;
}
.recipeDetails-author-photo {
  width: 2vw;
  height: 2vw;
  object-fit: cover;
  border-radius: 50%;
  border: 1px solid #212529;
}
.recipeDetails-close-icon {
  font-size: 2vw;
}
.recipeDetails-close-icon:hover {
  color: #ee6670;
  cursor: pointer;
}
.recipeDetails-picture {
  width: 15vw;
  height: 15vw;
  object-fit: cover;
  border: 1px solid #572529;
  border-radius: 20px;
  padding: 10px;
}
.recipeDetails-ingredients-text {
  font-family: "OpenSans-SemiBold";
  font-size: 0.9vw;
}
.recipeDetails-ingredients-item {
  font-family: "OpenSans-Regular";
  font-size: 0.8vw;
  text-align: justify;
  word-wrap: break-word;
  white-space: pre-wrap;
}
.recipeDetails-ingredients-description {
  font-family: "OpenSans-Regular";
  font-size: 1vw;
  text-align: justify;
  word-wrap: break-word;
  white-space: pre-wrap;
  background-color: #f3f6f8;
  padding: 2%;
  border-radius: 5px;
}
.recipeDetails-comments-area {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 2%;
}
.recipeDetails-comments-photo {
  width: 1vw;
  height: 1vw;
  margin-right: 2%;
  border-radius: 50%;
}
.recipeDetails-comments-icon {
  font-size: 1.6vw;
  margin-top: 1%;
  margin-right: 1.5%;
}
.recipeDetails-comments-content-area {
  width: 35vw;
}
.recipeDetails-comments-author {
  display: flex;
  justify-content: space-between;
  font-family: "OpenSans-SemiBold";
  font-size: 0.9vw;
}
.recipeDetails-comments-content {
  font-family: "OpenSans-Regular";
  text-align: justify;
  font-size: 0.9vw;
  word-wrap: break-word;
  white-space: pre-wrap;
  background-color: #f3f6f8;
  padding: 2%;
  border-radius: 5px;
}
.recipeDetails-comments-timeago {
  width: 6.7vw;
  margin-right: 5px;
  font-family: "OpenSans-SemiBold";
  font-size: 0.8vw;
  white-space: nowrap;
}
.recipeDetails-comments-rate {
  display: flex;
  flex-direction: row;
  font-family: "OpenSans-SemiBold";
  font-size: 0.8vw;
}
.recipeDetails-comments-stars {
  font-size: 1vw;
  color: #2e303f;
}
.recipeDetails-comments-stars-detail {
  font-size: 1vw;
  color: #3c4049;
}
.recipeDetails-comments-stars-active-details {
  font-size: 1vw;
  color: #ee4a70;
}
.recipeDetails-comments-stars-active-list {
  font-size: 1vw;
  color: #baae3e;
}
.recipeDetails-rate-outter {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 5.6vw;
}
.recipeDetails-rate-inner {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  white-space: nowrap;
  overflow: hidden;
  width: 0;
}
.recipesList-item-rate-outter {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 5.6vw;
}
.recipesList-item-rate-inner {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  white-space: nowrap;
  overflow: hidden;
  width: 0;
}
.recipeDetails-comments-edit-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  font-size: 1.1vw;
}
.recipeDetails-comments-edit-icon {
  margin-right: 15px;
}
.recipeDetails-comments-edit-icon:hover {
  cursor: pointer;
  color: #be6a15;
}
.recipeDetails-comments-trash-icon:hover {
  cursor: pointer;
  color: #ac3f21;
}
.recipebook-recipes-details {
  position: absolute;
  top: 0;
  right: 0;
  opacity: 1;
  overflow: hidden;
}
.recipebook-recipes-buttons {
  position: relative;
  opacity: 0;
  overflow: hidden;
}
.recipeDetails-player-wrapper {
  position: relative;
  padding-top: 56.25%;
}
.recipeDetails-player {
  position: absolute;
  top: 0;
  left: 0;
}
.recipebook-left-arrow {
  font-size: 2vw;
  color: #3c4049;
}
.recipebook-left-arrow:hover {
  font-size: 2vw;
  color: #2e303f;
  cursor: pointer;
}
.recipebook-right-arrow {
  font-size: 2vw;
  color: #3c4049;
}
.recipebook-right-arrow:hover {
  font-size: 2vw;
  color: #2e303f;
  cursor: pointer;
}
.recipebook-arrows-inactive {
  font-size: 2vw;
  color: #3c4049;
  opacity: 0.7;
}
